.user {
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  background-color: rgb(231, 236, 242);
  width: 426px;
  padding-top: 90px;
  padding-bottom: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.profile {
  margin-bottom: 0;
  border: 1px solid transparent;
  border-radius: 2%;
  overflow: hidden;
  box-shadow: 1px 1px 1px 1px rgba(34, 60, 80, 0.1);
  width: 250px;
  text-align: center;
}

.description {
  margin: 0;
  padding: 30px;

  background-color: #ffffff;
}

.avatar {
  width: 100px;
  border-radius: 50%;
  margin-bottom: 22px;
}

.name {
  margin: 0;
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: 700;
}

.tag {
  margin: 0;
  margin-bottom: 9px;
  font-size: 14px;

  color: rgb(198, 204, 210);
}

.location {
  margin: 0;
  font-size: 14px;

  color: rgb(198, 204, 210);
}
