.stats {
  margin: 0;
  padding: 0;

  display: flex;

  list-style: none;
}
.stats__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 17px;
  padding-bottom: 21px;
  width: calc(100% / 3);

  background-color: rgb(243, 246, 249);
  border-top: 1px solid rgb(230, 237, 240);
}
.stats__item:not(:last-child) {
  border-right: 1px solid rgb(230, 237, 240);
}
.label {
  font-size: 12px;
  margin-bottom: 4px;

  color: rgb(190, 198, 206);
}

.quantity {
  font-size: 14px;
  font-weight: 700;
}
